import React,{useState} from 'react';
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';
import {Form, Carousel, Container, Row, Table, Modal, Button, Col} from 'react-bootstrap';


class ResultsTable extends React.Component{
  constructor(props){
    super(props)
  }
  render(){
    return(
      <>       
        <h4 align="center">Maintenance Checklist</h4>
 
        <Table striped bordered hover variant="info" >                    
          <thead>
            <tr>
              <th>Task</th>
              <th>Interval</th>
              <th>Units</th>
              <th>Task Description</th>
              <th>Notes</th>
              <th>Last Done</th>
              <th>Next Due</th>
              <th>Time To Go</th>
              <th></th>
            </tr>
          </thead>
        <tbody>
          <tr>
            <td align="center" colSpan="9"><i>AIRCRAFT SCHEDULE MAINTENANCE</i></td>
          </tr>

            {this.props.main.map(item=>{ 
              
              if((item.cat==='asm')&&(item.current= true)){
                return(
                  <tr>
                    <td><i>{item.task}</i></td><td>{item.interval}</td><td>{item.units}</td><td>{item.taskDesc}</td><td>{item.notes}</td><td>{item.lastDone}</td><td>{item.nextDue}</td><td>{item.timeToGo}</td><td><UpdateModal id={item}/></td>
                  </tr>
                )
                  }

            })}
           

          <tr>
            <td align="center"  colSpan="9"><i>COMPONENT / ADDITIONAL INSPECTION SCHEDULE</i></td>
          </tr>
          {this.props.main.map(item=>{ 
              if((item.cat==='cais')&&(item.current= true)){
                return(
                  <tr>
                    <td><i>{item.task}</i></td><td>{item.interval}</td><td>{item.units}</td><td>{item.taskDesc}</td><td>{item.notes}</td><td>{item.lastDone}</td><td>{item.nextDue}</td><td>{item.timeToGo}</td><td><UpdateModal id={item}/></td>
                  </tr>
                )
                  }

            })}          
          <tr>
            <td align="center"  colSpan="9"><i>AIRWORTHINESS  DIRECTIVES</i></td>
          </tr>
          <tr>
            <td align="center"  colSpan="3"><i>Airframe</i></td>
          </tr>
          {this.props.main.map(item=>{ 
              if((item.cat==='ad')&&(item.current= true)){
                return(
                  <tr>
                    <td><i>{item.task}</i></td><td>{item.interval}</td><td>{item.units}</td><td>{item.taskDesc}</td><td>{item.notes}</td><td>{item.lastDone}</td><td>{item.nextDue}</td><td>{item.timeToGo}</td><td><UpdateModal id={item}/></td>
                  </tr>
                )
                  }

            })}         
            <tr>
           <td align="center"  colSpan="3"><i>LH Propeller</i></td>
         </tr>
         {this.props.main.map(item=>{ 
              if((item.cat==='lhprop')&&(item.current= true)){
                return(
                  <tr>
                    <td><i>{item.task}</i></td><td>{item.interval}</td><td>{item.units}</td><td>{item.taskDesc}</td><td>{item.notes}</td><td>{item.lastDone}</td><td>{item.nextDue}</td><td>{item.timeToGo}</td><td><UpdateModal id={item}/></td>
                  </tr>
                )
                  }

            })}         
          <tr>
           <td align="center"  colSpan="3"><i>RH Propeller</i></td>
          </tr>
          {this.props.main.map(item=>{ 
              if((item.cat==='rhprop')&&(item.current= true)){
                return(
                  <tr>
                    <td><i>{item.task}</i></td><td>{item.interval}</td><td>{item.units}</td><td>{item.taskDesc}</td><td>{item.notes}</td><td>{item.lastDone}</td><td>{item.nextDue}</td><td>{item.timeToGo}</td><td><UpdateModal id={item}/></td>
                  </tr>
                )
                  }

            })}          
          <tr>
            <td><i>LH Engine</i></td>
          </tr>
          {this.props.main.map(item=>{
             
              if((item.cat==='lheng')&&(item.current= true)){
                return(
                  <tr>
                    <td><i>{item.task}</i></td><td>{item.interval}</td><td>{item.units}</td><td>{item.taskDesc}</td><td>{item.notes}</td><td>{item.lastDone}</td><td>{item.nextDue}</td><td>{item.timeToGo}</td><td><UpdateModal id={item}/></td>
                  </tr>
                )
                  }

            })}          
          <tr>
            <td><i>RH Engine</i></td>
          </tr>
          {this.props.main.map(item=>{ 
              if((item.cat==='rheng')&&(item.current= true)){
                return(
                  <tr>
                    <td><i>{item.task}</i></td><td>{item.interval}</td><td>{item.units}</td><td>{item.taskDesc}</td><td>{item.notes}</td><td>{item.lastDone}</td><td>{item.nextDue}</td><td>{item.timeToGo}</td><td><UpdateModal id={item}/></td>
                  </tr>
                )
                  }

            })}                 
        </tbody>
        </Table>
         <p> 
             </p>

      </>
    )
  }
}

class Aircraft extends React.Component {
  constructor(props){
    super(props)
    this.state ={
      date: new Date().toDateString(),
    }
  }
  

  render(){
  return(<>
  {this.props.sum.map(item => {
    
    if(item.registeration !==undefined  ){
      return(<div> 
        <Row className="mx-0">
        <Col>
        <b>Registeration:</b> <i>{item.registeration}</i>
         <br/><b>Aircraft Model:</b> <i>{item.aircraftModel}</i>
         <br/><b>SerialNo:</b> <i>{item.serialNo}</i>
         <br/><b>Year of build:</b> <i>{item.yearOfBuild}</i>
        </Col>
        <Col>
        <b>Total Airframe Hours:</b> <i>{item.totalAirframeHours}</i>
         <br/><b>Total Airframe Landings:</b> <i>{item.totalAirframeLandings}</i> 
         <br/><b>Report Date:</b> <i>{this.state.date}</i>  
           </Col>
         </Row>
         <br/><div><Inc pd={this.props.sum}/></div>
         </div>)
    }

                                            
  })}
      
    </>)
}}

class Cessna extends React.Component{
  constructor(){
    super()
    this.state={
      ASN:'SN2198',
      q:'?sn=',
      balus:[]
      } 
  }
  componentDidMount(){
    fetch(window.location.origin + '/balus' + this.state.q + this.state.ASN)
    .then((res)=> res.json())
    .then((data)=> {this.setState({balus:data})
    })
  }
  render(){
    	return(<div>
                    <Aircraft sum={this.state.balus}/>
                    <ResultsTable main={this.state.balus}/>
            </div>)}
}

