class Dash8 extends React.Component{
  constructor(){
    super()
    this.state={
      ASN:'SN2200',
      q:'?sn=',
        balus:[]
      } 
  }
  componentDidMount(){
    fetch(window.location.origin + '/balus' + this.state.q + this.state.ASN )
    .then((res)=> res.json())
    .then((data)=> { this.setState({balus:data})
    })
  }
  render(){return (<div>
  <Aircraft sum={this.state.balus}/>
  <ResultsTable main={this.state.balus}/>
  </div>)
  }}

  const Inc = (props) => {
    const [show,setShow] = useState(false);
    const handleClose = ()=>{setShow(false)};
    const handleShow = ()=>{setShow(true)};
    
    
    return (<>
    
    <Button variant="outline-dark" bg="dark" size="lg" block onClick={handleShow}>Update Hours and Landings</Button>
          <Modal show={show} onHide={handleClose}>
              <Modal.Header closeButton>
                  <Modal.Title>Maintenance changes </Modal.Title>
              </Modal.Header>
              <Modal.Body>
              
           <IncForm fd={props.pd} />       
              </Modal.Body>
              <Modal.Footer>
                  <Button variant="secondary" onClick={handleClose}>
                   Close
                  </Button>
                  
            </Modal.Footer>
  
          </Modal>
    </>)
  };

  class IncForm extends React.Component{
    constructor(props){
      super(props)
  
     
    }
  
  componentDidMount(){
    console.log(this.props.fd)
  }
    render(){
         
      return (
        <Form method="POST" action="http://localhost:8000/update" >
          <fieldset>
            <legend>
              Update details:
            </legend>
  
            <label>
              Total Airframe Hours:<br /><input type="text" name="tah" defaultValue={this.props.fd[0].totalAirframeHours} size="30" maxlength="100" />
            </label>
            <label>
              Total Airframe Landings:<br /><input type="text" name="tal" defaultValue={this.props.fd[0].totalAirframeLandings} size="30" maxlength="100" />
            </label><br/>
            <label>
              licenseNo:<br /><input type="text" name="license" size="30" maxlength="100" />
            </label>
            <label><br/>
              Code:<br /><input type="text" name="code" size="30" maxlength="100" />
            </label>
              <br /><input type="hidden" name='sn' value={this.props.fd[0].serialNo} size="30" maxlength="100" />
              <br /><input type="hidden" name='model' value={this.props.fd[0].aircraftModel} size="30" maxlength="100" />
            </fieldset>
  
          <Button variant="primary" type="submit" >
            Confirm
          </Button>
        </Form>
      )
    }
    
         
   };

