 const UpdateModal = (props) => {
  const [show,setShow] = useState(false);
  const handleClose = ()=>{setShow(false)};
  const handleShow = ()=>{setShow(true)};
  
  
  return (<>
  
  <Button variant="outline-dark" bg="dark" size="lg" block onClick={handleShow}>Update</Button>
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>Maintenance changes {props.id.task + props.id.createdAt}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <UpdateForm ent={props.id}/>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                 Close
                </Button>
                
          </Modal.Footer>

        </Modal>
  </>)
};

class Menu extends React.Component{
  constructor(){
    super()

  }
  
  render(){
    return (<div>

  <Balusslide/>

<Router>
<div>
  <Switch>
    <Route path="/cessna">
      <Cessna/>
    </Route>
    <Route path="/islander">
      <Islander/>
    </Route>
    <Route path="/twinotter">
      <Twinotter/>
    </Route>
    <Route path="/dash8">
      <Dash8/>
    </Route>
  </Switch>
</div>
</Router>

      </div>
    )
  }
}

class Balusslide extends React.Component {
constructor(props){
  super(props)
this.state ={
  index:Number(localStorage.getItem('dindex')),
  handleSelect : (selectedIndex, e)=>{
    this.setState({index:selectedIndex});  
  },
   handleSlid : (activeIndex)=>{
    let cstuff = Object.entries(document.getElementsByClassName('active carousel-item'))[0][1].firstElementChild.src
    let clink = cstuff.slice(0,cstuff.indexOf('.'))
    localStorage.setItem('dindex',activeIndex)
    window.location.href=clink 
  }
  
}
}
componentDidUpdate(){
  if(!localStorage.getItem('dindex')){
    localStorage.setItem('dindex',0)
  }
  
}

render(){
   return(
      <>
        <Carousel interval={null} activeIndex={this.state.index} onSelect={this.state.handleSelect} onSlid={this.state.handleSlid}>
        <Carousel.Item >
         <img 
            className="d-block w-100"
            src="dash8.jpg"
            alt="first slide"  
         />
        </Carousel.Item>
        
        <Carousel.Item>
         <img 
            className="d-block w-100"
            src="twinotter.jpg"
            alt="second slide" 
         />
        </Carousel.Item>
         <Carousel.Item>
         <img 
            className="d-block w-100"
            src="islander.jpeg"
            alt="third slide" 
         />
        </Carousel.Item>
        <Carousel.Item>
         <img 
            className="d-block w-100"
            src="cessna.jpg"
            alt="fourth slide" 
         />
        </Carousel.Item>
      </Carousel>
      </>
    )
}

}

class App extends React.Component {
 

  render(){
    return(
      <>
      <Container>
      <Menu/>
      </Container>
      </>
      );
  }    

};

export default App;

